.Gallery {
	ul {
		width: 100%;
		&::after {
			content: '';
			display: block;
			clear: both;
		}
		li {
			width: 20%;
			height: 300px;
			float: left;
			padding: 10px;

			.inner {
				width: 100%;
				height: 100%;
				background: #fff;
				box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
				padding: 10px;
				min-height: 500px;

				.pic {
					width: 100%;
					height: 180px;
					margin-bottom: 20px;

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}

				h2 {
					font: normal 14px/1.2 'arial';
					color: #777;
				}
			}
		}
	}
}
